	module.exports = {"data":{"talkJson":{"description":"Bringing Angular Apps to Life with Animation\n\nOn April 3rd, AngularJS 1.1.4 will be released. The primary feature in this release is declarative support for animation in templates. Along with sharing our design decisions and implementation details, we'll show you how to use these new features and how to handle progressive enhancement for older browsers.\n\nMiško Hevery works as an Agile Coach at Google where he is responsible for coaching Googlers to maintain the high level of automated testing culture. He is the father of the AngularJS framework (http://angularjs.org) which enhances HTML to make it suitable for building single page (AJAX) web-applications.","meta":{"duration":"PT42M38S"},"presenter":{"name":null},"slug":"bringing-angular-apps-to-life-with-animation-by-misko-hevery","source":{"id":"cF_JsA9KsDM","provider":"youtube"},"tag":["gdg gtug angular.js"],"title":"Bringing Angular Apps to Life with Animation by Miško Hevery"},"allTalkJson":null},"pathContext":{"slug":"bringing-angular-apps-to-life-with-animation-by-misko-hevery","tags":["gdg gtug angular.js"]}}

